"use strict";

function parts() {
  // console.log(2)
  function load() {
    var options = {
      // http://303z9z3029.qicp.vip/
      url: "http://303z9z3029.qicp.vip/shoplist",
      success: render,
      dataType: "json",
      // category_id=185&page=1&sort=sort&num=20&type=shop&channel_id=1001
      data: {
        category_id: "185",
        page: 1,
        sort: "sort",
        num: 30,
        type: "shop",
        channel_id: 1001
      }
    };
    ajax(options);
  }

  function render(res) {
    console.log(res);
    var data = res.data; // console.log(data)

    var html = ""; // console.log(data)

    data.list.forEach(function (item) {
      // console.log(item)
      html += "\n        <section   class=\"spu-item-normal-box flex-item spu-item-normal-box\" gotoskuid=\"\" spuid=\"1000602\">\n        <figure  class=\"item-cover\">\n         <img  data-lazywidth=\"216\" data-lazyheight=\"216\" data-src=\"".concat(item.spuInfo.images, "\" src=\"").concat(item.spuInfo.images, "\" lazy=\"loaded\" />\n        </figure>\n        <article >\n         <h3 >").concat(item.spuInfo.spuTitle, "</h3>\n         <h5  class=\"txt-product-title\">").concat(item.spuInfo.activeTitle, "</h5>\n        </article>\n        <article  class=\"item-price\">\n         <span >\uFFE5").concat(item.spuInfo.discountPrice, "</span>\n         <span  class=\"orignal-price\">\uFFE5").concat(item.spuInfo.price, "</span>\n        </article>\n        <div  class=\"activity-tag\">\n         <span data-v-03828e8f=\"\"  class=\"yellow\"> \u5927\u4FC3 </span>\n        </div>\n        <div  class=\"markup-tag\"></div>\n       </section>"); // console.log(item.spuInfo)
      // var arry = [];
      // arry.push(item.spuInfo)
    });
    _("#parts").innerHTML = html;
    $("#parts img").lazyload({
      placeholder: "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsQAAA7EAZUrDhsAAAANSURBVBhXYzh8+PB/AAffA0nNPuCLAAAAAElFTkSuQmCC",
      effect: "fadeIn"
    });
  }

  load();
}

parts();